body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight:normal;
  /* background  */
  /*background: linear-gradient(89deg, #FF5EDF 0%, #04C8DE 100%); /* w3c */
  background: #e3dedd;
  color: #404040;
  min-height: 100%;
  overflow-x: hidden;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}




.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}




h1,h2,h3,h4,h5,h6,legend{
    font-family:"Roboto Slab","ff-tisa-web-pro","Georgia",Arial,sans-serif;
    font-weight:700;
    margin-top:0;
}

h1{
    color: #4e3163;
    font-size:175%;
}

.subtitle{
    font-size:95%; /* of h1 */
}

h2{
    color: #4e3163;
    font-size:150%;
}

h3{
    color: #6c3163;
    font-size:125%;
}

h4{
    font-size:115%;
}

h5{
    font-size:110%;
}


#content{
    background:#fff;
    height:100%;
    margin-left:25%;
    /* margin:auto; */
    max-width:800px;
    min-height:100%;
    padding:1.618em 3.236em;
    text-align: left;
}

blockquote{
    background-color: #F0F0F0;
    border-left:5px solid #CCCCCC;
    font-style:italic;
    line-height:24px;
    margin:0px 0px 24px 0px;
    /* margin-left:24px; */
    padding: 6px 20px;
}


ul,ol,dl{
    line-height:24px;
    list-style-image:none;
    /* list-style:none; */
    margin:0px 0px 24px 0px;
    padding:0;
}


li{
    margin-left: 24px;
}

dd{
    margin:0;
}


#content .section ul,#content .toctree-wrapper ul,article ul{
    list-style:disc;
    line-height:24px;
    margin-bottom:24px
}

#content .section ul li,#content .toctree-wrapper ul li,article ul li{
    list-style:disc;
    margin-left:24px
}

#content .section ul li p:last-child,#content .toctree-wrapper ul li p:last-child,article ul li p:last-child{
    margin-bottom:0
}


#content .section ul li ul,#content .toctree-wrapper ul li ul,article ul li ul{
    margin-bottom:0
}

#content .section ul li li,#content .toctree-wrapper ul li li,article ul li li{
    list-style:circle
}

#content .section ul li li li,#content .toctree-wrapper ul li li li,article ul li li li{
    list-style:square
}

#content .section ul li ol li,#content .toctree-wrapper ul li ol li,article ul li ol li{
    list-style:decimal
}

#content .section ol li,#content ol li,article ol li{
    list-style:decimal;
    margin-left:24px
}

#content .section ol li ul,#content ol li ul,article ol li ul{
    margin-bottom:0
}

#content .section ol li ul li,#content ol li ul li,article ol li ul li{
    list-style:disc
}

dl dt{
    font-weight:bold;
}

dl p,dl table,dl ul,dl ol{
    margin-bottom:12px !important;
}

dl dd{
    margin:0 0 12px 24px;
}

@media screen and (max-width: 768px){
    #content{
        margin-left:0}

    #content #content{
        padding:1.618em}

    #content.shift{
        position:fixed;
        min-width:100%;
        left:85%;
        top:0;
        height:100%;
        overflow:hidden}
}

#toggle-sidebar {
    display: none;
}

@media screen and (max-width: 768px) {
    #toggle-sidebar {
        display: block;
        background-color: #E0D6E9;
        text-align: center;
        padding: 0.6em;
        margin-bottom: 0.6em;
    }
    #toggle-sidebar h2 {
        margin: 0;
        padding: 0;
    }
}

#table-of-contents li{
    list-style:none;
    margin-left: 0px;
}

#table-of-contents header{
    height:32px;
    display:inline-block;
    line-height:32px;
    padding:0 1.618em;
    display:block;
    font-weight:bold;
    text-transform:uppercase;
    font-size:80%;
    color:#2980B9;
    white-space:nowrap
}

#table-of-contents ul{
    margin-bottom:0
}

#table-of-contents li.divide-top{
    border-top:solid 1px #404040
}

#table-of-contents li.divide-bottom{
    border-bottom:solid 1px #404040
}

#table-of-contents li.current{
    background:#e3e3e3
}

#table-of-contents li.current a{
    color:gray;
    border-right:solid 1px #c9c9c9;
    padding:0.4045em 2.427em
}

#table-of-contents li.current a:hover{
    background:#d6d6d6
}
    
#table-of-contents li a{
    /* color:#404040; */

    padding:0.4045em 1.618em;
    position:relative;

    /* background:#fcfcfc; */

    border:none;

    /* border-bottom:solid 1px #c9c9c9; */
    /* border-top:solid 1px #c9c9c9; */
    
    padding-left:1.618em -4px
}

#table-of-contents li.on a:hover,#table-of-contents li.current>a:hover{
    background:#fcfcfc
}

#table-of-contents li ul li a{
    /* background:#c9c9c9; */
    padding:0.4045em 2.427em
}

#table-of-contents li ul li ul li a{
    padding:0.4045em 3.236em
}

#table-of-contents li.current ul{
    display:block
}

#table-of-contents .local-toc li ul{
    display:block
}

#table-of-contents li ul li a{
    margin-bottom:0;
    color:#b3b3b3;
    font-weight:normal
}

#table-of-contents a{
    display:inline-block;
    line-height:18px;
    padding:0.4045em 1.618em;
    display:block;
    position:relative;
    font-size:90%;
    color:#b3b3b3;
    direction: ltr;
}

#table-of-contents a:hover{
    background-color:#4e4a4a;
    cursor:pointer
}


#table-of-contents{
    position:fixed;
    top:0;
    left:0;
    width:25%;
    overflow-x:hidden;
    overflow-y:scroll;
    height:100%;
    background:#343131;
    z-index:200;
    scrollbar-base-color: #1F1D1D;
    scrollbar-arrow-color: #b3b3b3;
    scrollbar-shadow-color: #1F1D1D;
    scrollbar-track-color : #343131;
}

#table-of-contents h2 a {
    display: none;
}

#table-of-contents h2{
    z-index:200;
    background-color:#715ab1;
    text-align:center;
    padding:0.809em;
    display:block;
    color:#fcfcfc;
    font-size: 100%;
    margin-bottom:0.809em}

@media screen and (max-width: 768px) {
    #table-of-contents {
        display: none;
        width: 60%;
    }

    #table-of-contents h2 a {
        display: block;
    }

    #table-of-contents:target {
        display: block;
    }

}
/* #text-table-of-contents {
    overflow:scroll;
    text-align: left; 
} */


ul.nav li ul li {
    display: none;
}

ul.nav li ul li ul li {
    display: none;
}

ul.nav li.active ul li {
    display: inline;
}

ul.nav li.active ul li ul li {
    display: inline;
}

ul.nav li.active ul li a {
    background-color: #E3E3E3;
    color: #8099B0;
    border-right:solid 1px #c9c9c9 !important;
}

ul.nav li.active ul li.active a {
    background-color: #C9C9C9;
    color: black !important;
    font-weight: bold !important;
}

ul.nav li.active ul li.active ul li.active a {
    color: black !important;
    font-weight: bold !important;
    display: block !important;
}

ul.nav li.active ul li.active ul li a {
    color: #808080 !important;
    font-weight: normal !important;
    display: block !important;
}

ul.nav li.active ul li ul li a {
    display: none !important;
}

/* / */
ul.nav li ul li ul li ul li {
    display: none !important; /* as long as nav is on multiple levels of ul */
    /* display: none; /* as long as nav is on multiple levels of ul *\/ */
}

ul.nav li.active > a {
    border-bottom:solid 1px #c9c9c9 !important; /* XXX Restrict it to 2nd level */
    border-right:solid 1px #c9c9c9 !important;
}

ul.nav li.active a {
    color: gray !important;
    font-weight:bold;
    background-color: white;
    border-right:solid 0px white !important;
}

ul.nav > li.active > a {
    color: black !important;
}

